'use client';

import { useState, useMemo } from 'react';
import {
  DndContext,
  DragOverlay,
  closestCenter,
  KeyboardSensor,
  PointerSensor,
  useSensor,
  useSensors,
  DragStartEvent,
  DragEndEvent,
  DragOverEvent,
} from '@dnd-kit/core';
import {
  arrayMove,
  SortableContext,
  sortableKeyboardCoordinates,
  verticalListSortingStrategy,
} from '@dnd-kit/sortable';
import { format, parseISO, eachDayOfInterval, addDays } from 'date-fns';
import { tripInfo, activities } from '@/data/tripData';
import { Activity, ScheduledItem } from '@/types/trip';
import ActivityCard from '@/components/ActivityCard';
import DayColumn from '@/components/DayColumn';
import FlightCard from '@/components/FlightCard';
import HotelCard from '@/components/HotelCard';
import ActivityPool from '@/components/ActivityPool';
import TripHeader from '@/components/TripHeader';
import {
  Plane,
  Calendar,
  Filter,
  Sparkles,
  RotateCcw,
  ChevronLeft,
  ChevronRight,
} from 'lucide-react';

export default function Home() {
  // Generate all trip days
  const tripDays = useMemo(() => {
    const start = parseISO(tripInfo.startDate);
    const end = parseISO(tripInfo.endDate);
    return eachDayOfInterval({ start, end });
  }, []);

  // State for scheduled items per day
  const [schedule, setSchedule] = useState<Record<string, ScheduledItem[]>>(() => {
    const initial: Record<string, ScheduledItem[]> = {};
    tripDays.forEach(day => {
      initial[format(day, 'yyyy-MM-dd')] = [];
    });
    return initial;
  });

  // State for unscheduled activities (pool)
  const [unscheduled, setUnscheduled] = useState<string[]>(
    activities.map(a => a.id)
  );

  // Filter state
  const [categoryFilter, setCategoryFilter] = useState<string | null>(null);
  const [cityFilter, setCityFilter] = useState<string | null>(null);

  // Drag state
  const [activeId, setActiveId] = useState<string | null>(null);
  const [activeActivity, setActiveActivity] = useState<Activity | null>(null);

  // Week view state
  const [weekStart, setWeekStart] = useState(0);
  const visibleDays = tripDays.slice(weekStart, weekStart + 7);

  const sensors = useSensors(
    useSensor(PointerSensor, {
      activationConstraint: {
        distance: 8,
      },
    }),
    useSensor(KeyboardSensor, {
      coordinateGetter: sortableKeyboardCoordinates,
    })
  );

  // Get activity by ID
  const getActivity = (id: string) => activities.find(a => a.id === id);

  // Filter activities
  const filteredActivities = useMemo(() => {
    return unscheduled.filter(id => {
      const activity = getActivity(id);
      if (!activity) return false;
      if (categoryFilter && activity.category !== categoryFilter) return false;
      if (cityFilter && activity.city !== cityFilter) return false;
      return true;
    });
  }, [unscheduled, categoryFilter, cityFilter]);

  // Handle drag start
  const handleDragStart = (event: DragStartEvent) => {
    const { active } = event;
    setActiveId(active.id as string);

    // Find the activity being dragged
    const activityId = active.id.toString().replace('scheduled-', '').split('|')[0];
    setActiveActivity(getActivity(activityId) || null);
  };

  // Handle drag end
  const handleDragEnd = (event: DragEndEvent) => {
    const { active, over } = event;

    if (!over) {
      setActiveId(null);
      setActiveActivity(null);
      return;
    }

    const activeIdStr = active.id.toString();
    const overIdStr = over.id.toString();

    // Extract activity ID and source info
    const isFromPool = !activeIdStr.startsWith('scheduled-');
    const activityId = isFromPool
      ? activeIdStr
      : activeIdStr.replace('scheduled-', '').split('|')[0];

    // Determine target
    const isTargetDay = overIdStr.startsWith('day-');
    const targetDate = isTargetDay
      ? overIdStr.replace('day-', '')
      : overIdStr.startsWith('scheduled-')
        ? overIdStr.split('|')[1]
        : null;

    if (targetDate) {
      // Remove from pool if coming from there
      if (isFromPool) {
        setUnscheduled(prev => prev.filter(id => id !== activityId));
      } else {
        // Remove from previous day
        const sourceDate = activeIdStr.split('|')[1];
        if (sourceDate !== targetDate) {
          setSchedule(prev => ({
            ...prev,
            [sourceDate]: prev[sourceDate].filter(item => item.activityId !== activityId)
          }));
        }
      }

      // Add to target day
      setSchedule(prev => {
        const existing = prev[targetDate] || [];
        const alreadyExists = existing.some(item => item.activityId === activityId);

        if (alreadyExists) return prev;

        const newItem: ScheduledItem = {
          id: `${activityId}-${targetDate}`,
          activityId,
          date: targetDate,
        };

        return {
          ...prev,
          [targetDate]: [...existing, newItem]
        };
      });
    } else if (overIdStr === 'activity-pool' && !isFromPool) {
      // Moving back to pool
      const sourceDate = activeIdStr.split('|')[1];
      setSchedule(prev => ({
        ...prev,
        [sourceDate]: prev[sourceDate].filter(item => item.activityId !== activityId)
      }));
      setUnscheduled(prev => [...prev, activityId]);
    }

    setActiveId(null);
    setActiveActivity(null);
  };

  // Auto-schedule suggestions
  const autoSchedule = () => {
    const newSchedule = { ...schedule };
    const toRemove: string[] = [];

    activities.forEach(activity => {
      if (!unscheduled.includes(activity.id)) return;

      // If activity has a specific date, schedule it there
      if (activity.date) {
        const existing = newSchedule[activity.date] || [];
        if (!existing.some(item => item.activityId === activity.id)) {
          newSchedule[activity.date] = [
            ...existing,
            {
              id: `${activity.id}-${activity.date}`,
              activityId: activity.id,
              date: activity.date,
              startTime: activity.time,
            }
          ];
          toRemove.push(activity.id);
        }
      }
    });

    setSchedule(newSchedule);
    setUnscheduled(prev => prev.filter(id => !toRemove.includes(id)));
  };

  // Reset schedule
  const resetSchedule = () => {
    const empty: Record<string, ScheduledItem[]> = {};
    tripDays.forEach(day => {
      empty[format(day, 'yyyy-MM-dd')] = [];
    });
    setSchedule(empty);
    setUnscheduled(activities.map(a => a.id));
  };

  // Get special items for a day (flights, hotels)
  const getFlightsForDay = (date: string) =>
    tripInfo.flights.filter(f => f.date === date);

  const getHotelCheckIn = (date: string) =>
    tripInfo.hotels.filter(h => h.checkIn === date);

  const getHotelCheckOut = (date: string) =>
    tripInfo.hotels.filter(h => h.checkOut === date);

  // Categories for filtering
  const categories = [
    { id: 'opera', label: 'Opera', color: 'bg-purple-100 text-purple-700' },
    { id: 'symphony', label: 'Symphony', color: 'bg-indigo-100 text-indigo-700' },
    { id: 'art', label: 'Art', color: 'bg-pink-100 text-pink-700' },
    { id: 'nature', label: 'Nature', color: 'bg-green-100 text-green-700' },
    { id: 'sightseeing', label: 'Sightseeing', color: 'bg-amber-100 text-amber-700' },
  ];

  const cities = [
    { id: 'seattle', label: 'Seattle' },
    { id: 'portland', label: 'Portland' },
    { id: 'tacoma', label: 'Tacoma' },
  ];

  return (
    <DndContext
      sensors={sensors}
      collisionDetection={closestCenter}
      onDragStart={handleDragStart}
      onDragEnd={handleDragEnd}
    >
      <div className="min-h-screen bg-gray-50">
        <TripHeader tripInfo={tripInfo} />

        <main className="max-w-[1800px] mx-auto px-4 py-4">
          <div className="flex gap-4">
            {/* Left sidebar - Activity Pool */}
            <div className="w-80 shrink-0">
              <div className="sticky top-4 space-y-4">
                {/* Filters */}
                <div className="bg-white rounded-xl shadow-sm border border-gray-100 p-4">
                  <div className="flex items-center gap-2 text-sm font-medium text-gray-700 mb-3">
                    <Filter className="w-4 h-4" />
                    Filters
                  </div>

                  {/* Category filters */}
                  <div className="flex flex-wrap gap-1.5 mb-3">
                    {categories.map(cat => (
                      <button
                        key={cat.id}
                        onClick={() => setCategoryFilter(
                          categoryFilter === cat.id ? null : cat.id
                        )}
                        className={`px-2.5 py-1 rounded-full text-xs font-medium transition-all ${
                          categoryFilter === cat.id
                            ? cat.color + ' ring-2 ring-offset-1 ring-gray-300'
                            : 'bg-gray-100 text-gray-600 hover:bg-gray-200'
                        }`}
                      >
                        {cat.label}
                      </button>
                    ))}
                  </div>

                  {/* City filters */}
                  <div className="flex flex-wrap gap-1.5">
                    {cities.map(city => (
                      <button
                        key={city.id}
                        onClick={() => setCityFilter(
                          cityFilter === city.id ? null : city.id
                        )}
                        className={`px-2.5 py-1 rounded-full text-xs font-medium transition-all ${
                          cityFilter === city.id
                            ? 'bg-blue-100 text-blue-700 ring-2 ring-offset-1 ring-blue-300'
                            : 'bg-gray-100 text-gray-600 hover:bg-gray-200'
                        }`}
                      >
                        {city.label}
                      </button>
                    ))}
                  </div>
                </div>

                {/* Action buttons */}
                <div className="flex gap-2">
                  <button
                    onClick={autoSchedule}
                    className="flex-1 flex items-center justify-center gap-2 px-3 py-2 bg-blue-500 text-white rounded-lg text-sm font-medium hover:bg-blue-600 transition-colors"
                  >
                    <Sparkles className="w-4 h-4" />
                    Auto-Schedule
                  </button>
                  <button
                    onClick={resetSchedule}
                    className="flex items-center justify-center gap-2 px-3 py-2 bg-gray-200 text-gray-700 rounded-lg text-sm font-medium hover:bg-gray-300 transition-colors"
                  >
                    <RotateCcw className="w-4 h-4" />
                  </button>
                </div>

                {/* Activity Pool */}
                <ActivityPool
                  activityIds={filteredActivities}
                  getActivity={getActivity}
                />
              </div>
            </div>

            {/* Main content - Calendar */}
            <div className="flex-1 overflow-hidden">
              {/* Week navigation */}
              <div className="flex items-center justify-between mb-4">
                <button
                  onClick={() => setWeekStart(Math.max(0, weekStart - 7))}
                  disabled={weekStart === 0}
                  className="p-2 rounded-lg hover:bg-gray-200 disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
                >
                  <ChevronLeft className="w-5 h-5" />
                </button>
                <div className="text-sm font-medium text-gray-600">
                  {format(visibleDays[0], 'MMM d')} - {format(visibleDays[visibleDays.length - 1], 'MMM d, yyyy')}
                </div>
                <button
                  onClick={() => setWeekStart(Math.min(tripDays.length - 7, weekStart + 7))}
                  disabled={weekStart >= tripDays.length - 7}
                  className="p-2 rounded-lg hover:bg-gray-200 disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
                >
                  <ChevronRight className="w-5 h-5" />
                </button>
              </div>

              {/* Day columns */}
              <div className="grid grid-cols-7 gap-3">
                {visibleDays.map(day => {
                  const dateStr = format(day, 'yyyy-MM-dd');
                  const daySchedule = schedule[dateStr] || [];
                  const flights = getFlightsForDay(dateStr);
                  const hotelCheckins = getHotelCheckIn(dateStr);
                  const hotelCheckouts = getHotelCheckOut(dateStr);

                  return (
                    <DayColumn
                      key={dateStr}
                      date={day}
                      items={daySchedule}
                      flights={flights}
                      hotelCheckins={hotelCheckins}
                      hotelCheckouts={hotelCheckouts}
                      getActivity={getActivity}
                    />
                  );
                })}
              </div>
            </div>
          </div>
        </main>

        {/* Drag overlay */}
        <DragOverlay>
          {activeActivity && (
            <div className="opacity-90">
              <ActivityCard activity={activeActivity} isDragging />
            </div>
          )}
        </DragOverlay>
      </div>
    </DndContext>
  );
}
